/* FlipFade.css */

/* Fade-in animation for images */
.fade-in {
  animation: fadeIn 1s ease-in forwards;
}

@keyframes fadeIn {
  from {
    opacity: 0;
    transform: translateY(20px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}

/* Magic reveal animation when image is revealed */
@keyframes revealMagic {
  0% {
    opacity: 0;
    transform: scale(0.8) rotate(-10deg);
    filter: brightness(2);
  }
  100% {
    opacity: 1;
    transform: scale(1) rotate(0deg);
    filter: brightness(1);
  }
}

.magic-reveal {
  animation: revealMagic 1s ease-out;
}

/* Container for 3D card perspective */
.card-container {
  width: 300px;
  height: 400px;
  perspective: 1000px;
  cursor: pointer;
  margin: 1rem;
}

/* Card styling and flip transition */
.card {
  width: 100%;
  height: 100%;
  position: relative;
  transform-style: preserve-3d;
  transition: transform 0.8s ease-in-out;
}

.card.flipped {
  transform: rotateY(180deg);
}

/* Shared card face styling */
.card-front,
.card-back {
  position: absolute;
  width: 100%;
  height: 100%;
  backface-visibility: hidden;
  border-radius: 12px;
  box-shadow: 0 6px 10px rgba(0, 0, 0, 0.25);
  overflow: hidden;
}

/* Front of the card */
.card-front {
  background-color: #ffffff;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  padding: 1rem;
}

.card-front img {
  width: 100%;
  height: 100%;
  object-fit: cover;
  border-radius: 8px;
  opacity: 0;
  animation: fadeIn 1s ease-in forwards;
}

/* Back of the card */
.card-back {
  background-color: #ffffff;
  color: #1a1a1a;
  transform: rotateY(180deg);
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding: 1rem;
  text-align: center;
}

.card-back h2 {
  font-size: 1.4rem;
  margin-bottom: 0.5rem;
}

.card-back p {
  font-size: 1rem;
  margin-bottom: 1rem;
}

.card-back a {
  padding: 0.5rem 1rem;
  background-color: #fcd34d;
  border-radius: 6px;
  font-weight: bold;
  color: #1a1a1a;
  text-decoration: none;
  transition: background-color 0.3s;
}

.card-back a:hover {
  background-color: #fbbf24;
}
