.splash {
  background-color: black;
  color: gold;
  height: 100vh;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  font-family: serif;
 transition: opacity 2.5s ease-in-out, background-color 2.5s ease-in-out;
  opacity: 1;
}

.splash.fade-out {
  opacity: 0;
  background-color: white; /* كأن الصفحة بتنور */
}
.lumos-flash {
  position: absolute;
  width: 100px;
  height: 100px;
  background: radial-gradient(circle, rgba(255, 255, 200, 0.9), transparent 70%);
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%) scale(0);
  border-radius: 50%;
  z-index: 2;
  pointer-events: none;
}

.splash.fade-out .lumos-flash {
  animation: lumos-expand 2.5s ease-out forwards;
}

@keyframes lumos-expand {
  0% {
    transform: translate(-50%, -50%) scale(0);
    opacity: 1;
  }
  50% {
    transform: translate(-50%, -50%) scale(3);
    opacity: 0.8;
  }
  100% {
    transform: translate(-50%, -50%) scale(8);
    opacity: 0;
  }
}


.card-container {
  perspective: 1000px;
  width: 240px;
  height: 340px;
}

.card {
  width: 100%;
  height: 100%;
  position: relative;
  transform-style: preserve-3d;
  transition: transform 0.8s ease-in-out;
}

.card-container:hover .card {
  transform: rotateY(180deg);
}

.card-front, .card-back {
  position: absolute;
  width: 100%;
  height: 100%;
  border-radius: 15px;
  overflow: hidden;
  backface-visibility: hidden;
}

.card-front {
  z-index: 2;
}

.card-back {
  transform: rotateY(180deg);
  z-index: 1;
}
